/* eslint-disable camelcase */
import PropTypes from 'prop-types';
import { Page, View, Text, Image, Document } from '@react-pdf/renderer';
// utils
import { fCurrency } from '../../../../utils/formatNumber';
//
import styles from './InvioceStyle';

// ----------------------------------------------------------------------

InvoicePDF.propTypes = {
  invoice: PropTypes.object.isRequired,
};

export default function InvoicePDF({ invoice }) {
  // eslint-disable-next-line camelcase
  const {  plan_status,message,total_number,total_correct_number,numbers,correct_number,wrong_number, sender_name, transid, total_wrong_number, plan_date,amount,oldbal,newbal,} = invoice;


  return (
    <Document>
      <Page size="A4" style={styles.page}>
        <View style={[styles.gridContainer, styles.mb40]}>
          <Image source="/upload/welcome.png" style={{ height: 32 }} />
          <View style={{ alignItems: 'flex-end', flexDirection: 'column' }}>
            <Text style={styles.h3}>{plan_status === 1 && 'successful' || plan_status === 0 && 'proccessing' || plan_status === 2 && 'failed'}</Text>
            <Text>{transid}</Text>
          </View>
        </View>

        <Text style={[styles.overline, styles.mb8]}> BULKSMS  Invoice Details</Text>

        <View style={styles.table}>
          
    
            <View style={styles.tableRow}>
              <View style={styles.tableCell_2}>
                <Text  style={styles.subtitle2}>Sender Name</Text>
              </View>    
              <Text>{sender_name}</Text>
            </View>
            <View style={styles.tableRow}>
              <View style={styles.tableCell_2}>
                <Text  style={styles.subtitle2}>Message</Text>
              </View>    
              <Text>{message}</Text>
            </View>
            <View style={styles.tableRow}>
              <View style={styles.tableCell_2}>
                <Text  style={styles.subtitle2}>Total Number</Text>
              </View>    
              <Text>{total_number}</Text>
            </View>
            <View style={styles.tableRow}>
              <View style={styles.tableCell_2}>
                <Text  style={styles.subtitle2}>Charges</Text>
              </View>    
              <Text>₦{fCurrency(amount)}</Text>
            </View>
            <View style={styles.tableRow}>
              <View style={styles.tableCell_2}>
                <Text  style={styles.subtitle2}>Old Balance</Text>
              </View>    
              <Text>₦{fCurrency(oldbal)}</Text>
            </View>
            <View style={styles.tableRow}>
              <View style={styles.tableCell_2}>
                <Text  style={styles.subtitle2}>New Balance</Text>
              </View>    
              <Text>₦{fCurrency(newbal)}</Text>
            </View>
            <View style={styles.tableRow}>
              <View style={styles.tableCell_2}>
                <Text  style={styles.subtitle2}>Total Correct Number</Text>
              </View>    
              <Text>{total_correct_number}</Text>
            </View>
            <View style={styles.tableRow}>
              <View style={styles.tableCell_2}>
                <Text  style={styles.subtitle2}>Total Wrong Number</Text>
              </View>    
              <Text>{total_wrong_number}</Text>
            </View>
            <View style={styles.tableRow}>
              <View style={styles.tableCell_2}>
                <Text  style={styles.subtitle2}>All Number</Text>
              </View>    
              <Text>{numbers}</Text>
            </View>
            <View style={styles.tableRow}>
              <View style={styles.tableCell_2}>
                <Text  style={styles.subtitle2}>Correct Number</Text>
              </View>    
              <Text>{correct_number}</Text>
            </View>
            <View style={styles.tableRow}>
              <View style={styles.tableCell_2}>
                <Text  style={styles.subtitle2}>Wrong Number</Text>
              </View>    
              <Text>{wrong_number}</Text>
            </View>
            <View style={styles.tableRow}>
              <View style={styles.tableCell_2}>
                <Text  style={styles.subtitle2}>Date</Text>
              </View>    
              <Text>{plan_date}</Text>
            </View>
        </View>

        <View style={[styles.gridContainer, styles.footer]}>
          <View style={styles.col8}>
            <Text style={styles.subtitle2}>NOTES</Text>
            <Text> We appreciate your continuous patronage. Your satisfaction is our top priority!</Text>
          </View>
          {/* <View style={[styles.col4, styles.alignRight]}>
            <Text style={styles.subtitle2}>any enquiry, kindly contact us @</Text>
          </View> */}
        </View>
      </Page>
    </Document>
  );
}
